import * as httpService from './Http';
export function adminLogin(phone, passwd) {
    return httpService.post('/api/AdminLogin', {
        phone: phone,
        passwd: passwd
    });
}
export function restauLogin(phone, passwd) {
    return httpService.post('/api/RestaurantLogin', {
        phone: phone,
        passwd: passwd
    });
}
export function checkLogin() {
    return httpService.post('/api/admin/AdminCheckLogin', {});
}



